@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.navmenu {
  width: 100% !important;
  box-shadow: 0px 0px 0px 0px rgba(116, 95, 95, 0.555) !important;

}

.nav {
  display: flex !important;
  transition: all 0.1s linear !important;
  position: fixed !important;
  z-index: 2000 !important;
  width: 100% !important;
  justify-content: center !important;
}

.scrollNav {
  transition: all 0.5s ease-in;
  z-index: 2000;
  position: fixed;
  background: #ffffff;
  width: 100%;
  border-bottom: 1px solid #dddddd;
  justify-content: center;
}




@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}

.logo {
  padding: 20px !important;
  z-index: 100 !important;
  animation: rotation 5s infinite linear;
  bottom: 50% !important;
  width: 100px;
  height: 100px;
  padding: 0px;
}

.logotext{
    font-family: 'Cookie', cursive;
    padding: 0px;
  }
  
.grid{
    display: grid;
    background-image: url(/mepopycakes/static/media/fondo.06cf1cbd.svg);
    grid-template-columns: 50% 40% 10%;
}   

.imagen{
padding: 80px 20px 30px 20px;
text-align: center;
width: 80%;
/* clip-path: polygon(50% 0%, 0% 100%, 100% 100%); */
}

.texto{
    font-family: 'Roboto Condensed', sans-serif;
    padding: 90px 10px 30px 90px;
    text-align: left;
    line-height: 0.1em;
    width: 68%;
    letter-spacing: 0.15em;
    font-size: calc(2vw)
}

.lateral{
    padding: 70px 0px 0px 0px;
    width: 100%;
    display: flex;
}


@media screen and (max-width:780px ){
    .grid{
        display: grid;
        grid-template-columns: 100%;
        grid-template-rows: auto auto auto ;
    }   

    .imagen{
        padding: 0px 60px 40px 60px;
        text-align: center;
        width: 100%;
    }

    .texto{
        padding: 70px 10px 30px 70px;
        text-align: center;
        width: 68%;
    }

    .lateral{
        display: none;
    }
}
.cardproduct:hover{
    box-shadow: 5px 10px 20px 1px rgba(116, 95, 95, 0.555) !important;
    transform: scale(1.05);
    transition:  all 0.1s linear !important;
}

.cardproduct{
    display: flex;
    justify-content: center;
    opacity: 1;
    
}

.productoContainer{
    padding-bottom: 1em;

}
.nombregrupo{
    font-family: 'Roboto Condensed', sans-serif;
}

.productos{
